.head-lib-bg {
  background-color: $white;
  display: none;
}

.head-lib {
  background-image: url('./../images/mobile-logged-bg.jpg');
  background-repeat: no-repeat;
  background-position: 50%, 50%;
  background-size: cover;
  padding: 40px 20px 92px 20px;
  max-width: 786px;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('./../images/mobile-logged-bg@2x.jpg');
  }
  @include smallmob {
    width: 280px;
  }
  @include tablet {
    // @media (min-width: 768px) and (max-width: 1024px) {
    background-image: url('./../images/tablet-logged-bg.jpg');
    max-width: 1280px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('./../images/tablet-logged-bg@2x.jpg');
      // }
    }
  }

  @include desktop {
    @media (min-width: 1200px) {
      background-image: url('./../images/desktop-logged-bg.jpg');
      max-width: 2200px;

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('./../images/desktop-logged-bg@2x.jpg');
      }
    }
  }
}

.library-header-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;

  @include tablet {
    gap: 32px;
  }
  @include desktop {
    gap: 16px;
  }
}

.library-header-button {
  width: 130px;
  height: 44px;
  border-radius: 5%;
  color: $white;
  font-family: $main-font;
  font-size: 12px;
  background-color: transparent;
  border: 1px solid $white;
  @include smallmob {
    width: 80px;
    height: 30px;
    font-size: 8px;
  }
  @include tablet {
    width: 152px;
  }
  @include desktop {
    width: 148px;
    height: 42px;
  }
}

.library-header-button:hover,
.library-header-button:focus {
  background-color: $flush-orange;
  border: none;
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .header-library {
    background-image: linear-gradient($bg-shadow, $bg-shadow),
      url('./../images/mobile-logged-bg@2x.jpg');
  }
}

